<template>
  <div class="movie-view has-header">
    <scroller title="最受关注的图书 | 虚构类" type="hasCover" :items="novel"></scroller>
    <scroller title="最受关注的图书 | 非虚构类" type="hasCover" :items="reality"></scroller>
    <scroller title="豆瓣纸书" type="hasCover" :items="travel">
      <div class="promItem" slot="promItem">
        <img src="../assets/book_zw.jpg" class="corver" alt="">
        <div class="content">
          <p class="name">造物</p>
          <p class="info">改变世界的万物图典，3000幅图里发明与冒险</p>
          <span class="price">&yen; 68</span>
        </div>
      </div>
    </scroller>
    <scroller title="发现好书" type="onlyString" :items="bookTags"></scroller>
    <types></types>
    <download-app></download-app>
  </div>
</template>
<script>
import { mapState } from 'vuex'

import Scroller from '../components/Scroller'
import Types from '../components/Types'
import DownloadApp from '../components/DownloadApp'

export default{
  name: 'book-view',
  components: { Scroller, Types, DownloadApp },
  data () {
    return {}
  },
  computed: {
    // Getting Vuex State from store/modules/book
    ...mapState({
      novel: state =>state.book.novel,
      reality: state => state.book.reality,
      travel: state => state.book.travel,
      bookTags: state => state.book.bookTags
    })
  },
  methods: {
    // Dispatching getBook
    getBook: function (){
      this.$store.dispatch('getBook')
    }
  },
  created: function created(){
    // Getting books data on created
    this.getBook()
  }
}
</script>

<style lang="stylus" scoped>
.promItem
  overflow hidden
  margin 1.6rem 1.8rem 0.8rem 1.6rem
  .corver
    float left
    width 10rem
    margin-right 1.5rem
  .content
    margin-right 1rem
    .name
      font-size 2rem
      color #494949
      margin 1rem
      max-height 2.2rem
      overflow hidden
      text-overflow ellipsis
      white-space nowrap
      word-wrap normal
    .price
      color #E76648
      font-size 1.6rem
      line-height 2.2rem
    .info
      font-size 1.3rem
      font-weight 300
      line-height 1.5
      color #9b9b9b
</style>


