<template>
  <div class="types">
    <h2>分类浏览</h2>
    <div class="content">
      <ul class="type-list">
        <li v-for="item in items">
          <a :href="baseUri + item.href">
            {{item.title}} <span></span>
          </a>
        </li>
        <li></li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default{
    name: 'types',
    data () {
      return {
        baseUri: 'https://m.douban.com/',
        items: [
          {
            title: '经典',
            href: 'movie/classic'
          },
          {
            title: '冷门佳片',
            href: 'movie/underrated'
          },
          {
            title: '豆瓣高分',
            href: 'movie/doubantop'
          },
          {
            title: '动作',
            href: 'movie/action'
          },
          {
            title: '喜剧',
            href: 'movie/comedy'
          },
          {
            title: '爱情',
            href: 'movie/love'
          },
          {
            title: '悬疑',
            href: 'movie/mystery'
          },
          {
            title: '恐怖',
            href: 'movie/horror'
          },
          {
            title: '科幻',
            href: 'movie/scifi'
          },
          {
            title: '治愈',
            href: 'movie/sweet'
          },
          {
            title: '文艺',
            href: 'movie/artfilm'
          },
          {
            title: '成长',
            href: 'movie/youth'
          },
          {
            title: '动画',
            href: 'movie/animation'
          },
          {
            title: '华语',
            href: 'movie/chinese'
          },
          {
            title: '欧美',
            href: 'movie/western'
          },
          {
            title: '韩国',
            href: 'movie/korean'
          },
          {
            title: '日本',
            href: 'movie/japanese'
          }
        ]
      }
    }
  }
</script>

<style lang="stylus" scoped>
.types
  margin-top 1rem
  h2
    height 2.6rem
    line-height 2.6rem
    padding 0 1.6rem
    display inline-block
  .content
    .type-list
      border-bottom 0.1rem solid #eee
      overflow hidden
      li
        float left
        box-sizing border-box
        width 50%
        padding-right 1.8rem
        text-align center
        heihgt 4.2rem
        line-height 4.2rem
        font-size 1.6rem
        border-top 0.1rem solid #eee
        border-right 0.1rem solid #eee
        a
          color #42bd56
          span
            color #ccc
            float right
            font-weight bold
            display inline-block
            border-right 0.1rem solid #ccc
            border-bottom: 0.1rem solid #ccc;
            width 0.8rem
            height 0.8rem
            transform rotate(-45deg)
            margin-top 1.8rem
      li:nth-child(2)
        padding-left 1.8rem
</style>


